@import"https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap";*{padding:0;margin:0;box-sizing:border-box;font-family:"Manrope",sans-serif}body{background-color:#1f2632;width:100vw;height:100vh;display:flex;align-items:center;justify-content:center}.App{width:600px;margin:1rem;min-height:350px;border-radius:15px;text-align:center;background-color:#323a49;padding:2rem 3rem;display:flex;flex-direction:column;gap:2rem;position:relative}.App .text{display:flex;flex-direction:column;align-items:center;justify-content:center;gap:2rem;min-height:225px}.App .text #advice-id{color:#52ffa8;font-size:.9rem;text-transform:uppercase;letter-spacing:5px;margin-top:1.25rem}.App .text .advice{flex-grow:1;position:relative;width:100%}.App .text .advice #advice-content{color:#cee3e9;quotes:"“" "”";font-size:1.75rem;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:100%}.App .text .advice #advice-content::after{content:close-quote}.App .text .advice #advice-content::before{content:open-quote}.App #divider{background-image:url(images/pattern-divider-desktop.svg);background-size:contain;background-repeat:no-repeat;height:20px;margin-bottom:2.5rem}.App button{width:65px;height:65px;border-radius:50%;outline:none;border:none;background-color:#52ffa8;background-image:url(images/icon-dice.svg);background-repeat:no-repeat;background-position:center center;margin:0 auto;position:absolute;bottom:-4rem;left:50%;transform:translate(-50%, -50%)}.App button:hover{cursor:pointer;box-shadow:0 0 25px 0px #52ffa8}@media screen and (max-width: 375px){.App{width:calc(375px - 2rem);margin:auto;padding:1.5rem 1.5rem}.App .text #advice-id{font-size:.75rem}.App .text #advice-content{font-size:1.4rem}.App #divider{background-image:url(images/pattern-divider-mobile.svg)}}